.app-header {
  // background-color: #bcd8db71;
  background-color: rgba(0, 0, 0, 0.45);
  backdrop-filter: blur(6px);
  min-height: 44px;
  max-height: 44px;
  color: #ffffff;
  padding: 6px 8px;
  width: 100vw;
z-index: 5;

  
  .jello {
    height: 30px;
    width: 30px;
    color: #fff;
    // margin-left: 0.5rem;
    position: relative;
  }

  .logo {
    margin-left: 0.5rem;
    font-size: rem(24px);
    letter-spacing: 1px;
  }

  ul {
    gap: 20px;
    &:last-child {
     
    }

    .boards-link{
      padding: 5px 8px;
      font-weight: 700;
      display: flex;
      justify-content: center;
      align-items: center;
      grid-gap: 5px;
      gap: 5px;
      background-color: #dfe0db3c;
      color: #fff;
      border-radius: 3px;
      transition: all .3s;
      min-width: fit-content;

      &:hover{
        background-color: #dfe0db60;
      }

      .board-list-icon{
        display: flex;
        align-self: center;
      }
    }

    .MuiAvatar-root {
      height: 32px;
      width: 32px;
      font-size: rem(14px);
      cursor: pointer;
    }
  }
}

@-webkit-keyframes wobble-top-on-hover {
  16.65% {
    -webkit-transform: skew(-12deg);
    transform: skew(-12deg);
  }
  33.3% {
    -webkit-transform: skew(10deg);
    transform: skew(10deg);
  }
  49.95% {
    -webkit-transform: skew(-6deg);
    transform: skew(-6deg);
  }
  66.6% {
    -webkit-transform: skew(4deg);
    transform: skew(4deg);
  }
  83.25% {
    -webkit-transform: skew(-2deg);
    transform: skew(-2deg);
  }
  100% {
    -webkit-transform: skew(0);
    transform: skew(0);
  }
}
@keyframes wobble-top-on-hover {
  16.65% {
    -webkit-transform: skew(-12deg);
    transform: skew(-12deg);
  }
  33.3% {
    -webkit-transform: skew(10deg);
    transform: skew(10deg);
  }
  49.95% {
    -webkit-transform: skew(-6deg);
    transform: skew(-6deg);
  }
  66.6% {
    -webkit-transform: skew(4deg);
    transform: skew(4deg);
  }
  83.25% {
    -webkit-transform: skew(-2deg);
    transform: skew(-2deg);
  }
  100% {
    -webkit-transform: skew(0);
    transform: skew(0);
  }
}
.wobble-top-on-hover {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-transform-origin: 0 100%;
  transform-origin: 0 100%;
}
.wobble-top-on-hover:hover,
.wobble-top-on-hover:focus,
.wobble-top-on-hover:active {
  -webkit-animation-name: wobble-top-on-hover;
  animation-name: wobble-top-on-hover;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
  -webkit-animation-iteration-count: 4;
  animation-iteration-count: 4;
}
