* {
  box-sizing: border-box;

}



html {
  color: #ffffff;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans,
    Ubuntu, Droid Sans, Helvetica Neue, sans-serif;
  overflow-y: hidden;

}

body {
  color: $dark-blue;
  margin: 0;
  font-size: 14px;
  overflow-y: hidden;
  min-height: 100vh;
}

.app-container {
  overflow: hidden;
  height: 100vh;
}

main {
  display: flex;
  flex-grow: 1;
  height: 0;
  // justify-content: center;
}

img {
  width: 100%;
  object-fit: cover;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

::selection {
  background-color: #0065ff87;
  color: #ffffff;
}

p {
  margin: 0 auto;
  // max-width: 50ch;
}

button {
  all: unset;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans,
    Ubuntu, Droid Sans, Helvetica Neue, sans-serif;
  cursor: pointer;
}

input,
textarea {
  font-family: unset;
  font-size: 14px;
  // color: unset;
  color: $dark-blue;
  outline: none;
  border: none;
  resize: none;
  width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

